
h1[data-v-8a5d0254]{
  font-weight: bold;
  font-size: 1.6em;
  margin-bottom: 12px;
}
.my-table[data-v-8a5d0254]{
  border-top:solid 1px lightgrey;
  border-bottom:solid 1px lightgrey;
}
thead td[data-v-8a5d0254]{
  font-weight: bold;
}
thead td[data-v-8a5d0254]:nth-child(1){
  width: 60%;
}
tbody td[data-v-8a5d0254]:nth-child(1){
  width: 20%;
