  text-align: left;
}
.my-table[data-v-8a5d0254]{
  width: 90%;
  margin:0 auto;
  font-size: 0.85em;
  height: 560px;
  overflow-y: scroll;
}
@media screen and (max-width: 1220px){
table td[data-v-8a5d0254]{
    font-size: 0.9em;
}
}
