    min-width: 140px;
  }*/
tbody td[data-v-8a5d0254]:nth-child(3){
    width: 3%;
    min-width: 5px;
}
tbody td:nth-child(3) a[data-v-8a5d0254]:nth-child(1){
    display: none;
}
