
.my-table[data-v-46270324]{
  border-top:solid 1px lightgrey;
  border-bottom:solid 1px lightgrey;
}
h1[data-v-46270324]{
  font-weight: bold;
  font-size: 1.6em;
  margin-bottom: 12px;
}
thead td[data-v-46270324]{
  font-weight: bold;
}
thead td[data-v-46270324]:nth-child(1){
  width: 60%;
}
tbody td[data-v-46270324]:nth-child(2){
  min-width: 80px;
}
tbody td[data-v-46270324]:nth-child(3){
  min-width: 100px;
}
table td[data-v-46270324]{
  text-align: left;
}
.my-table[data-v-46270324]{
  width: 90%;
  margin:0 auto;
  font-size: 0.85em;
  height: 560px;
  overflow-y: scroll;
}
@media screen and (max-width: 1220px){
table td[data-v-46270324]{
  font-size: 0.9em;
}
}
@media screen and (max-width: 768px){
table td[data-v-46270324]{
  font-size: 0.8em;
}
}
[data-v-46270324]::-webkit-scrollbar  
{  
    width: 8px;  
    height: 8px;  
    background-color: #F5F5F5;
}  
  
/*定义滚动条轨道 内阴影+圆角*/
[data-v-46270324]::-webkit-scrollbar-track  
{  
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);  
    border-radius: 10px;  
    background-color: #F5F5F5;
}  
  
/*定义滑块 内阴影+圆角*/
[data-v-46270324]::-webkit-scrollbar-thumb  
{  
    border-radius: 10px;  
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
    background-color: grey;
}  



